import api from "../../boot/axios";
import Utils from "@/utils/utils";
// configure this file based on your API structure
const ENDPOINT = "SalesTraceAuth/api/";

export default class AuthApi {
  //login
  loginApi = async (loginData) => {
    const response = await api
      .post(ENDPOINT + "Auth/login", loginData, {
        // withCredentials: true,
      })
      .then((res) => {
        if (res.status === 200 && res.data.isAuthenticated) {
          const resData = res.data;
          // console.log(resData);
          api.defaults.headers.common[
            "Authorization"
          ] = `Bearer ${resData.bearerToken}`;
          localStorage.setItem("jwt", resData.bearerToken);

          localStorage.setItem("user", resData.fullName);
          localStorage.setItem("userId", resData.userId);

          return true;
        } else {
          alert("wrong cred");
          return false;
        }
      })
      .catch((err) => {
        // console.log(err);
      });
    return response;
  };

  // logoutApi '/auth/logout/'
  logoutApi = async () => {
    // console.log("logout");
    // const response = await api.post("/auth/logout/", {});
    // api.defaults.headers.common["Authorization"] = "";
    localStorage.clear();
    return true;
  };
  //user-validate /token/verify/

  validateUser = async () => {
    if (!localStorage.getItem("jwt")) {
      return false;
    }

    const encodedJwt = localStorage.getItem("jwt");
    const utils = new Utils();
    let payload = utils.parseJwt(encodedJwt);
    // console.log(payload, "validate user ");
    const isValidated = utils.isDateExpire(payload.exp);
    // console.log(isValidated);
    if (!isValidated) {
      this.logoutApi();
      return false;
    }
    // validate user from backend
    // const token = {
    //   token: localStorage.getItem("jwt"),
    // };
    // const response = await api
    //   .post("/auth/token/verify/", token, {
    //     withCredentials: true,
    //   })
    //   .then((res) => {
    // console.log(res.status);
    //     if (res.status === 200) return true;

    //     return false;
    //   });
    return true;
  };
}
